import Vue from 'vue'
import VueRouter from 'vue-router'
import publicRoutes from './public.routes'
import adminRoutes from './admin.routes'

Vue.use(VueRouter)

const routes = [
    ...publicRoutes,
    ...adminRoutes
]

const router = new VueRouter({
    mode: 'history',
    base: '/',
    routes
})

export default router
