uto 0;\r\n}\r\n\r\n","/* Smart grid */\n@import 'smart-grid.scss';\n\n/* Fonts */\n@import 'fonts.scss';\n\n/* Variables */\n@import 'variables.scss';\n\n* {\n    font-family: $ff-regular;\n    box-sizing: border-box;\n}\n\nhtml,\nbody {\n    background-color: $body-bg;\n    font-family: $ff-regular;\n    color: #fff;\n    box-sizing: border-box;\n}\n\n.wrapper {\n    @include wrapper();\n    height: 100vh;\n}\n\n.d-flex {\n    display: flex;\n}\n\n.row {\n    @include row-flex()\n}\n\n.col {\n    @include col()\n}\n\n.col-1 {\n    @include size(1)\n}\n\n.col-2 {\n    @include size(2)\n}\n\n.col-3 {\n    @include size(3)\n}\n\n.col-4 {\n    @include size(4)\n}\n\n.col-5 {\n    @include size(5)\n}\n\n.col-6 {\n    @include size(6)\n}\n\n.col-7 {\n    @include size(7)\n}\n\n.col-8 {\n    @include size(8)\n}\n\n.col-9 {\n    @include size(9)\n}\n\n.col-10 {\n    @include size(10)\n}\n\n.col-11 {\n    @include size(11)\n}\n\n.col-12 {\n    @include size(12)\n}\n\n.shift-1 {\n    @include shift(1)\n}\n\n.shift-2 {\n    @include shift(2)\n}\n\n.shift-3 {\n    @include shift(3)\n}\n\n.shift-4 {\n    @include shift(4)\n}\n\n.shift-5 {\n    @include shift(5)\n}\n\n.shift-6 {\n    @include shift(6)\n}\n\n.h2 {\n    font-size: 25px;\n}\n\n.text-align-center {\n    text-align: center;\n}\n\n.my-2 {\n    margin-top: 20px;\n    margin-bottom: 20px;\n}\n\n.mt-1 {\n    margin-top: 10px;\n}\n\n.mt-2 {\n    margin-top: 20px;\n}\n\n.mt-3 {\n    margin-top: 30px;\n}\n\n.mt-4 {\n    margin-top: 40px;\n}\n\n.mw-100 {\n    min-width: 100%;\n}\n\n.text-center {\n    text-align: center;\n}\n\n.flex-nowrap {\n    flex-wrap: nowrap;\n}\n\n.pl-0 {\n    padding-left: 0;\n}\n","// Body\n$body-bg: #121212;\n\n// Typography\n@import url('https://fonts.googleapis.com/css?family=Lato');\n$ff-regular: 'Lato regular', sans-serif;\n$ff-light: 'Lato light', sans-serif;\n$ff-bold: 'Lato bold', sans-serif;\n$font-size-base: 0.9rem;\n$line-height-base: 1.6;\n\n// Colors\n$blue: #3490dc;\n$indigo: #6574cd;\n$purple: #9561e2;\n$pink: #f66d9b;\n$red: #e3342f;\n$orange: #f6993f;\n$yellow: #ffed4a;\n$green: #38c172;\n$teal: #4dc0b5;\n$cyan: #6cb2eb;\n$violet-light: #826eff;\n$color-primary: #8687E7;\n$color-bg: #363636;\n$color-bg-item: #272727;\n$color-border: #979797;\n$color-input-text: #cecece;\n","$columns: 10;\r\n$atom: (100% / $columns);\r\n\r\n$break_slg: 2560px;\r\n$break_lg: 1410px;\r\n$break_my: 1175px;\r\n$break_smd: 1100px;\r\n$break_md: 960px;\r\n$break_sm: 720px;\r\n$break_xs: 321px;\r\n\r\n$offset: 15px;\r\n$offset_slg: $offset;\r\n$offset_lg: $offset;\r\n$offset_my: $offset;\r\n$offset_smd: $offset;\r\n$offset_md: $offset;\r\n$offset_sm: $offset;\r\n$offset_xs: $offset;\r\n\r\n$offset_one_side: ($offset / 2);\r\n$offset_slg_one_side: ($offset_slg / 2);\r\n$offset_lg_one_side: ($offset_lg / 2);\r\n$offset_my_one_side: ($offset_my / 2);\r\n$offset_smd_one_side: ($offset_smd / 2);\r\n$offset_md_one_side: ($offset_md / 2);\r\n$offset_sm_one_side: ($offset_sm / 2);\r\n$offset_xs_one_side: ($offset_xs / 2);\r\n\r\n$fields: 15px;\r\n$fields_slg: 15px;\r\n$fields_lg: 15px;\r\n$fields_my: 15px;\r\n$fields_smd: 15px;\r\n$fields_md: 15px;\r\n$fields_sm: 10px;\r\n$fields_xs: 5px;\r\n\r\n@mixin wrapper-full(){\r\n    padding-left: $fields;\r\n    padding-right: $fields;\r\n\r\n    @include slg-block(){\r\n        padding-left: $fields_slg;\r\n        padding-right: $fields_slg;\r\n    }\r\n\r\n    @include lg-block(){\r\n        padding-left: $fields_lg;\r\n        padding-right: $fields_lg;\r\n    }\r\n\r\n    @include my-block(){\r\n        padding-left: $fields_my;\r\n        padding-right: $fields_my;\r\n    }\r\n\r\n    @include smd-block(){\r\n        padding-left: $fields_smd;\r\n        padding-right: $fields_smd;\r\n    }\r\n\r\n    @include md-block(){\r\n        padding-left: $fields_md;\r\n        padding-right: $fields_md;\r\n    }\r\n\r\n    @include sm-block(){\r\n        padding-left: $fields_sm;\r\n        padding-right: $fields_sm;\r\n    }\r\n\r\n    @include xs-block(){\r\n        padding-left: $fields_xs;\r\n        padding-right: $fields_xs;\r\n    }\r\n}\r\n\r\n@mixin row-offsets(){\r\n    margin-left: ($offset_one_side * -1);\r\n    margin-right: ($offset_one_side * -1);\r\n}\r\n\r\n@mixin col-offsets($type){\r\n    #{$type}-left: $offset_one_side;\r\n    #{$type}-right: $offset_one_side;\r\n}\r\n\r\n@mixin wrapper(){\r\n    max-width: 1410px;\r\n    margin: 0 auto;\r\n    @include wrapper-full();\r\n}\r\n\r\n@mixin row-flex(){\r\n    display: flex;\r\n    flex-wrap: wrap;\r\n    @include row-offsets();\r\n}\r\n\r\n@mixin row-float(){\r\n    @include row-offsets();\r\n    @include clearfix();\r\n}\r\n\r\n@mixin row-ib(){\r\n    @include row-offsets();\r\n}\r\n\r\n@mixin col(){\r\n    box-sizing: border-box;\r\n    word-wrap: break-word;\r\n    @include col-offsets(margin);\r\n}\r\n\r\n@mixin col-float(){\r\n    float: left;\r\n}\r\n\r\n@mixin col-ib(){\r\n    display: inline-block;\r\n    vertical-align: top;\r\n}\r\n\r\n@mixin col-padding(){\r\n    word-wrap: break-word;\r\n    @include col-offsets(padding);\r\n}\r\n\r\n@mixin size($n){\r\n    $val: 100% / $columns * $n;\r\n    width: #{calc(#{$val} - #{$offset})};\r\n}\r\n\r\n@mixin size-slg($n){\r\n    $val: 100% / $columns * $n;\r\n    @include slg(width, #{calc(#{$val} - #{$offset_slg})});\r\n}\r\n\r\n@mixin size-lg($n){\r\n    $val: 100% / $columns * $n;\r\n    @include lg(width, #{calc(#{$val} - #{$offset_lg})});\r\n}\r\n\r\n@mixin size-my($n){\r\n    $val: 100% / $columns * $n;\r\n    @include my(width, #{calc(#{$val} - #{$offset_my})});\r\n}\r\n\r\n@mixin size-smd($n){\r\n    $val: 100% / $columns * $n;\r\n    @include smd(width, #{calc(#{$val} - #{$offset_smd})});\r\n}\r\n\r\n@mixin size-md($n){\r\n    $val: 100% / $columns * $n;\r\n    @include md(width, #{calc(#{$val} - #{$offset_md})});\r\n}\r\n\r\n@mixin size-sm($n){\r\n    $val: 100% / $columns * $n;\r\n    @include sm(width, #{calc(#{$val} - #{$offset_sm})});\r\n}\r\n\r\n@mixin size-xs($n)