<template>
    <div>
        <router-link to="/admin" class="admin-link" v-if="user_role === 'Admin'">Admin panel</router-link>
    </div>
</template>

<script>

export default {
    name: "Index",
    components: {},
    data: () => ({
        user_role: false
    }),
    methods: {
        async init() {
            this.$auth.user()
            if (this.$auth.user().roles[0]) {
                this.user_role = this.$auth.user().roles[0]
                if (this.user_role === 'Admin') {
                    console.log(' - Admin');
                } else if (this.user_role === 'Editor') {
                    console.log(' - Editor');
                } else {
                    console.log(' - Guest');
                }
            }
        }
    },
    mounted() {
        this.init()
    }
}
</script>

